/*  */
header {
    position: fixed;    /*   ,    */
    top: 0;             /*     */
    width: 100%;        /*     */
    height: 60px;       /*   */
    z-index: 20;        /*     */
    background-color: rgba(0, 0, 0, 1);   /*   */   /**/
    color: white;       /*   */
    display: flex;          /*   flex   */
    align-items: center;    /*    */
    padding: 0 20px;        /*  20px   */
    box-sizing: border-box;      /*     */
  }
  
  /*   */
  .banner {
    top:3px;        /*   3px  */
    height: 60vh;       /*   60% */
    width: 100vw;       /*    */
    max-width: 100%;        /*   100%  */
    background-size: cover;     /*     */
    background-position: center;        /*     */
    color: white;       /*    */
    display: flex;       /*      */
    align-items: flex-end;      /*     */
    margin-top: 70px; /*     */
    position: relative;         /*    */
    margin-bottom: 0; /*    */
    z-index: 10; /*    */
    padding-top: 60px; /*      */
  }
  
  .banner-content {
    padding: 30px; /*   30px */
    background: linear-gradient(
      180deg,
      transparent 0%, /*   */
      rgba(0, 0, 0, 0.2) 20%, /* 20%   */
      rgba(0, 0, 0, 0.4) 40%, /*   */
      rgba(0, 0, 0, 0.6) 60%, /*   */
      rgba(0, 0, 0, 0.8) 80%, /*   */
      rgba(0, 0, 0, 0.95) 100% /*    */
    );
    width: 100%; /*       */
    box-sizing: border-box; /*     */
    display: flex; /*    */
    flex-direction: column; /*     */
    align-items: flex-start; /*     */
    /*position: relative;*/ /*      */
    /*z-index: 10;*/ /*      */
}

  .banner h1 {
    font-size: 3rem;        /*     */
    margin-bottom: 0.5rem;      /*    */
    text-shadow: 3px 3px 6px rgba(0,0,0,0.7); /*    */
    color:white;             /*   */
    font-weight: 700; /*   */
    letter-spacing: 1px; /*   */
  }
  
  .banner p {
    font-size: 1rem;        /*    */
    max-width: 700px;       /*    */
    margin-bottom: 1.5rem;        /*   */
    text-align: left;       /*   */
    text-shadow: 2px 2px 4px rgba(0,0,0,0.8); /*    */
    color:white;             /*   */
    font-weight: 400; /*    */
    /*line-height: 1.8;*/ /*   */
  }
  
  .play-btn, .info-btn {
    padding: 10px 20px;     /*   */
    margin-right: 10px;     /*   */
    border: none;       /*   */
    border-radius: 5px;     /*    */
    font-size: 1rem;        /*    */
    cursor: pointer;        /*    */
    /*  ,  */
  }
  
  .play-btn {
    background-color: white;        /*   */
    color: rgb(255, 44, 146);       /*   */ /*??*/
  }
  
  .info-btn {
    background-color: rgba(109, 109, 110, 0.7); /*    */
    color: rgb(0, 0, 0);       /*   */
  }
  
  .title-btn {
    margin-top: 5px;        /*   */
    width: 150px;       /*   */
  }
  
  /*  */ /*  ,..*/
  @media screen and (max-width: 1200px) {
    .banner {
        height: 120vh; /*      50% */
    }
    .banner h1 {
        font-size: 2.5rem;
    }
    .banner p {
        font-size: 0.9rem;
    }
    .play-btn, .info-btn {
        padding: 8px 16px;
        font-size: 0.9rem;
    }
    .title-btn {
        width: 120px;
        font-size: 0.85rem;
    }
  }
  
  @media screen and (max-width: 768px) {
    .banner {
        height: 50vh;
    }
    .banner h1 {
        font-size: 2rem;
    }
    .banner p {
        font-size: 0.8rem;
    }
    .play-btn, .info-btn {
        padding: 6px 12px;
        font-size: 0.8rem;
    }
    .title-btn {
        width: 60px;
        font-size: 0.75rem;
    }
  }
  
  @media screen and (max-width: 576px) {
    .banner {
        height: 70vh;
    }
    .banner h1 {
        font-size: 1.5rem;
    }
    .banner p {
        font-size: 0.7rem;
    }
    .play-btn, .info-btn {
        padding: 5px 10px;
        font-size: 0.7rem;
    }
    .title-btn {
        width: 80px;
        font-size: 0.7rem;
    }
  }