.home-popular {
    text-align: center;
  }
  
  .view-toggle-buttons {
    display: flex; /* Flexbox    */
    justify-content: center; /*    */
    align-items: center; /*     */
    margin: 100px 0 20px;
    padding-right: 30px;
    float: none; /* float  */
    gap: 10px; /*    */
  }
  
  .view-toggle-buttons button {
    margin: 0; /*    */
    padding: 10px 20px;
    font-size: 0.7em;
    cursor: pointer;
    width: 120px;
    height: 50px;
    background-color: #ddd;
    border: none;
    border-radius: 5px;
    transition: background-color 0.3s;
  }
  
  .view-toggle-buttons button:hover {
    background-color: #ccc;
  }
  
  .view-toggle-buttons .active {
    font-weight: bold;
    text-decoration: underline;
    background-color: red;
    color: white;
  }
  
  