:root {
    --container-start-x: -50%;
    --container-end-x: -90%;
    --container-start-y: -58%;
    --container-end-y: -42%;
    --container-start-color: #b0b0b0; /*   */
    --container-end-color: #3f3f3f; /*   */
    --button-color: #2c5f2d;
  }
  
  /*    */
  .bg-image {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #000000; /*   */
  }
  
  .bg-image::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #000000; /*   */
  }
  
  .container {
    height: 100vh;
    width: 100vw;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: transparent;
  }
  
  #phone {
    width: 400px; /*   */
    height: 400px; /*   */
    position: relative;
    perspective: 400px; /* 3D    */
    transform-style: preserve-3d;
  }
  
  #content-wrapper {
    width: 100%;
    height: 100%;
    position: relative;
    transform-style: preserve-3d;
    transition: transform 0.8s cubic-bezier(0.25, 0.8, 0.25, 1); /*    */
  }

  .card {
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: white;
    border-radius: 15px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
    backface-visibility: hidden; /*   */
    padding: 20px; /*  20px   */
    box-sizing: border-box; /*       */
  }
  
  
  #login {
    transform: rotateY(0deg); /*    */
  }
  
  #register {
    transform: rotateY(180deg); /*    */
  }
  
  .login-active #content-wrapper {
    transform: rotateY(0deg); /*    */
  }
  
  .register-active #content-wrapper {
    transform: rotateY(180deg); /*    */
  }
  
  /*    */
  .input {
    position: relative;
    margin-top: 1.5rem;
  }
  
  .input input {
    background-color: transparent;
    border: none;
    border-bottom: 1px solid #9e9e9e;
    outline: none;
    height: 1.5rem;
    width: 100%;
    font-size: 1rem;
    transition: all 0.3s ease;
  }
  
  .input label {
    color: #9e9e9e;
    position: absolute;
    top: 30%;
    left: 0;
    transform-origin: 0% 100%;
    transform: translateY(0);
    pointer-events: none;
    transition: all 0.2s ease-out;
  }
  
  .input.active label {
    transform: translateY(-1.5rem);
    font-size: 0.8rem;
    color: #7f7f7f;
  }
  
  /*   */
  .checkbox {
    margin-top: 2rem;
    margin-bottom: 2rem;
    display: flex;
    align-items: center;
  }
  
  .checkbox input {
    margin-right: 10px;
  }
  
  .checkbox label {
    color: #3f3f3f;
    font-weight: 900;
  }
  
  /*   */
  button {
    display: block;
    border-radius: 30px;
    border: none;
    width: 100%;
    max-width: 100%;
    background-color: #f6d7ff !important; /*   */
    color: #552f7d;
    padding: 10px;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 0.9rem;
    box-shadow: 0px 5px 15px rgba(63, 63, 63, 0.3);
    transition: all 0.2s ease;
  }
  
  button:hover {
    box-shadow: 0px 2px 10px rgba(47, 29, 111, 0.4);
  }
  
  /*     */
  .account-check {
    margin-top: 1rem;
    text-align: center;
    font-size: 0.85rem;
    font-weight: 600;
    text-decoration: none;
  }

  .read-text {
    color: #000000 !important;
  }
  
  .account-check:hover {
    color: #ff0000;
    text-decoration: underline;
  }
  h1 {
    color: black;
    display: flex; /*    */
    justify-content: center; /*    */
  
    text-align: center; /*    */
    height: 100%; /*     */
    margin: 0; /*    */
  }