.movie-table {
    width: 80%;
    margin: 100px auto 0; 
  }
  .movie-table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
    border: 2px solid #ff2f2f;
  }
  
  .movie-table th,
  .movie-table td {
    border: 1px solid #ddd;
    padding: 12px;
    text-align: left;
  }
  
  .movie-table td {
 
    padding: 12px;
    text-align: left;
    background-color: rgba(255, 220, 220, 0.7); /*     */
  }
  
  .movie-table th {
  
    background-color: #ffffff;
    color: #000000; /*     */
    font-weight: bold;
  
    background-color: #ffffff; /*     */
    font-weight: bold;
    padding: 12px;
    text-align: left;

    color: #333; /*    */
  }
  
  .movie-row {
    border-bottom: 1px solid #ddd;
  }
  
  .movie-row:hover {
    background-color: #000000;
  }
  
  .movie-table tr:hover {
    background-color: rgba(255, 36, 36, 0.7); /*   ,   */
    transition: background-color 0.2s ease;
  }
  
  .movie-table tr:hover td {
    background-color: rgba(255, 255, 255, 0.7);
    color: #000; /*      */
    transition: background-color 0.2s ease;
  }
  
  .poster-cell {
    width: 100px;
    padding: 8px;
  }
  
  .poster-cell img {
    width: 80px;
    height: 120px;
    object-fit: cover;
    border-radius: 4px;
  }
  
  .title-cell {
    font-weight: bold;
  }
  
  .rating-cell .rating {
    display: flex;
    align-items: center;
    gap: 5px;
  }
  
  .star {
    color: #ddd;
  }
  
  .star.filled {
    color: #ffd700;
  }
  
  .overview-cell {
    max-width: 300px;
  }
  
  .overview {
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
  
  .pagination {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-top: 20px;
  }
  
  .pagination button {
    padding: 8px 16px;
    border: none;
    background-color: #007bff;
    color: white;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .pagination button:disabled {
    background-color: #ccc;
  
  }
  
  .pagination-controls {
    display: flex;
    justify-content: center;
    gap: 5px;
  }
  
  .pagination-controls button {
    padding: 5px;
    font-size: 0.9em;
  
    width: auto; /*      */
  }
  
  /*   */
  .star {
    color: #ddd;
  }
  
  .star.filled {
    color: #FFD700; /*   */
    width: auto; 
  }